.invite .top {
    background: yellow;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.invite .top p {
    margin: 0px;
    padding: 20px;
    font-size: 30px;
}
.invite .top i {
    font-size: 20px;
}
.invite .top i:hover {
    cursor: pointer;
}
.invite .item {
    padding: 10px;
    text-align: left;
}
.invite .item > div {
    display: flex;
    align-items: center;
}
.invite .item > div i {
    margin-right: 10px;
}
.invite > button {
    padding: 5px 90px;
    border-radius: 20px;
    margin-top: 40px;
    font-size: 18px;
}
.invite p.customer {
    font-size: 20px;
    color: #1976d2;
    font-weight: bold;
}