.name{
    padding: 20px;
    padding-bottom: 0;
    margin-bottom: 0;
    font-weight: 700;
    font-size:  18px;
    color: #000000
}
.doc{
    color: #1976d2;
    font-weight: 500;
    font-size: 25px;
}
.count{
    color: #020202;
    font-size: 30px;
}
#filled-start-adornment{
    color: #1976d2 !important;
    font-weight: 600;
    letter-spacing: 1px;
    font-family: Arial, Helvetica, monospace;
    z-index: 1;
}
.total{
    color: #1976d2;
    font-size: 30px;
    font-weight: 600;
}
.adorment{
    color:#63a1df;
    font-weight: 600;
    font-size: 30px;
    padding-right: -10px;
    overflow: hidden;
}
.compose{
    position: relative;
}
.compose::after{
    position: absolute;
    left: 53px;
    top:  26px;
    font-weight: 600;
    content: attr(placeholder);
    font-size: 58px;
    color:#63a1df;
    pointer-events: none;  
    font-family: Arial, Helvetica, monospace;
    letter-spacing: 1px;
    opacity: 0.6;

}