.admin button {
    margin-right: 10px;
}
.admin th, td {
    text-align: center;
}
.admin tr{
    
  border-bottom: 1px solid #ddd;
}
.admin table {
    
  border-collapse: collapse;
    width: 90%;
    margin: auto;
    margin-top: 15% !important;
}
.admin > div:first-child {
    padding: 20px 20px 0px 20px;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.admin table i {
    font-size: 18px;
    padding-left: 5px;
}
.admin > i:nth-child(2) {
        float: right;
    padding-right: 10%;
    padding-top: 4%;
    font-size: 25px;
}
.admin i:hover {
    cursor: pointer;
}